}

.loader {
  margin: 0 auto;
  height: 30px;
  width: 30px;
  background-color: #ff6600;
  border-radius: 50%;
  animation: pulsing 2s infinite;
  transition: opacity .5s, visibility .5s;
  opacity: 1;
}

.loader.stop {
  opacity: 0;
  visibility: hidden;
}

@keyframes pulsing {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }
  50% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(0.5);
  }
}

